.runPlanList {
  transition: ease-in 3s;
  border: 2px solid rgb(0, 0, 0);
  display: flex;
  justify-content: space-around;
  padding: 1rem;
}

.runPlanItem{
  height: 25rem;
  width: 12%;
  display: flex;
  background-color: #f6f2f2;
  filter: drop-shadow(0px 0px 5px #d56e19);
  border: 2px solid black;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  flex-wrap: wrap;
}

.runPlanTicked{
  height: 25rem;
  width: 12%;
  display: flex;
  background-color: #ff7300;
  filter: drop-shadow(0px 0px 5px #d56e19);
  border: 2px solid black;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  flex-wrap: wrap;
}

.infoBlock {
    height: 3.7rem;
    width: 100%;
    font-size: 1rem;
    text-align: center;
    margin: 0 0;
    border-bottom: 5px solid rgb(0, 0, 0);    
}

.lastInfoBlock{
  height: 3.7rem;
  width: 100%;
  font-size: 1rem;
  text-align: center;
  margin: 0 0;
}


.checkBox {
    height: 3rem;
    width: 3rem;
   
}

.leftDirectionButton {
    position: absolute;
    left: 2rem;
    margin-top: 0.5rem;
    width: 3rem;
    height: 2rem;
    border-radius: 1.2rem;
    border: 2px solid black;
    padding-bottom: 1rem;
    background-color: #121212;
    color: #f6f2f2;
    text-align:center ;
    font-size: 2rem;
  }

  .rightDirectionButton {
    position: absolute;
    right: 2rem;
    margin-top: 0.5rem;
    width: 3rem;
    height: 2rem;
    border-radius: 1.2rem;
    border: 2px solid black;
    padding-bottom: 1rem;
    background-color: #121212;
    color: #f6f2f2;
    text-align:center ;
    font-size: 2rem;
  }

.rightDirectionButton:hover,
.leftDirectionButton:hover,
.rightDirectionButton:active,
.leftDirectionButton:active {
  background-color: #ff7300;
  cursor: pointer;
}

.runPlanContainer {
  display: flex;
  margin: 0 auto 2rem auto;
  width: 95%;
  flex-wrap: wrap;
  justify-content: space-between;
  }

.weekContainer{
    width: 100%;
  }

@media only screen and (min-width:601px) and (max-width: 999px){
  .runPlanList {
    display: flex;
    padding: 1rem;
    flex-wrap: wrap;
    flex-direction: column;
}
 
.runPlanItem{
  height: 4rem;
  width: 100%;
  display: flex;
  margin: 0.3rem auto;
}

.runPlanTicked{
  height: 4rem;
  width: 100%;
  display: flex;
  margin: 0.3rem auto;
  background-color: #ff7300;
}

.infoBlock {
  height: 4rem;
  width: 20%;
  font-size: 1rem;
  border-left: 2px solid black;
  border-bottom: none;
  padding-top: 1.3rem;
}

.lastInfoBlock{
  height: 3.5rem;
  width: 28%;
  border-left: 2px solid black;
  border-bottom: none;
  padding-top: 1rem;
}

.checkBox {
  height: 2rem;
  width: 2rem;
}
}


@media only screen and (max-width: 600px){


.runPlanList {
    display: flex;
    padding: 0.5rem;
    flex-wrap: wrap;
    flex-direction: column;
}

.runPlanItem{
  height: 4rem;
  width: 99%;
  display: flex;
  margin: 0.3rem auto;
}

.runPlanTicked{
  height: 4rem;
  width: 100%;
  display: flex;
  margin: 0.3rem auto;
  background-color: #ff7300;
}

.infoBlock {
  height: 4rem;
  width: 20%;
  font-size: 0.8rem;
  border-left: 2px solid black;
  border-bottom: none;
  padding-top: 1.3rem;
}

.lastInfoBlock{
  height: 3.5rem;
  width: 26%;
  font-size: 0.7rem;
  border-left: 2px solid black;
  border-bottom: none;
  padding-top: 1rem;
}

.checkBox {
  height: 1.5rem;
  width: 1.5rem;
}
}

