/* app.cs and New Run Component style*/
@import url('https://fonts.googleapis.com/css2?family=DynaPuff:wght@400;500;600;700&family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');
/*font-family:'Ubuntu', sans-serif;*/
@import url('https://fonts.googleapis.com/css2?family=DynaPuff:wght@400;500;600;700&family=Lexend+Peta:wght@100;200;300;400;500;600;700;800;900&family=Lilita+One&family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500&display=swap');
/*font-family: 'Lexend Peta', sans-serif;*/
@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@400;500;600;700&family=DynaPuff:wght@400;500;600;700&family=Lexend+Peta:wght@100;200;300;400;500;600;700;800;900&family=Lilita+One&family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500&display=swap');
/* font-family: 'Caveat', cursive;*/
body {
  background-color: #ffffff;
  font-family:'Ubuntu', sans-serif;
}

.app-header {
  font-weight: bold;
  height: 100%;
  text-align: center;
  background-color: #ffffff;
  font-family:'Ubuntu', sans-serif;
}

section {
  color: #97fb57;  
  color: #FEDE24;
  color: #121212;
  color: #909090;
  color: #f6f2f2;
  color: #f1600d;
  color: #f9b918;
  color: #ff7300;
}

.runningPages {
  display: flex;
  justify-content: space-evenly;
  margin-top: 1rem;
  border-bottom: 2px solid #121212; 
}

h1 {
  color: #121212;
  text-align: center;
  margin: 0 auto;
}

h2 {
  text-align: center;
  color: #121212;  
}

ul{
  list-style-type: none;
}

p {
  color:#000000;
 
}

input {
  width: 6rem;
  height: 2rem;
  margin-left: 0.2rem;
  border: solid black;
}

#runContent {
  width: 100%;
}

.logo {
  background-image: url('runnerLogo.png');
  background-position: center;
  background-size: cover;
  height: 6rem;
  width: 20rem;
  margin: 0 auto;
  cursor: pointer;
}

.runnerLogo{
  width: 100%;
  width: 24rem;
  font-family:'Lexend Peta', sans-serif;
  font-weight: 800;
  cursor: pointer;
}

.run1, .run2, .run3, .run4, .run5, .run6 {
  background-position: center;
  background-size: cover;
  height: 8rem;
  width: 8rem;
  border-radius: 10px;
}

.run1 {
  background-image: url('./Runner\ images/OIG.jpg');
 
}

.run2 {
  background-image: url('./Runner\ images/OIG (9).jpg');
}

.run3 {
  background-image: url('./Runner\ images/OIG (10).jpg');
}

.run4 {
  background-image: url('./Runner\ images/OIG (1).jpg');
}

.run5 {
  background-image: url('./Runner\ images/OIG (16).jpg');
}

.run6 {
  background-image: url('./Runner\ images/OIG.54ESNNh.jpg');
}


/*New Run Components */
.formRow {
  height: 100%;
  width: 60%;
  margin: 1rem auto;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  border: 2px solid rgb(0, 0, 0);
  border-radius: 25px;
  background-color: #ff7300;
}

.formEntry {
  width: 100%;
  height: 100%;
  margin: 1rem auto ;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
}

.addBtn {
  color: #ffffff;
  margin: 0 auto;
  border-radius: 2rem;
  font-size: 0.8rem;
  padding: 0.7rem;
  background-color: #020202;
  margin-bottom: 0.3rem;
}

.runningList {
  margin: 0 auto;
  padding: 0;
  text-align: center;
}

li {
  width: 80%;
  margin: 1rem auto;
  border: 2px solid black;
  background-color: aliceblue;
  /*filter: drop-shadow(0px 0px 5px #d56e19); */
  display: flex;
}

.listRun {
  height: 9rem;
  width: 60%;
  margin: 1rem auto;
  border: 2px solid black;
  border-radius: 15px;
  background-color: #f6f2f2;
  padding: 0.5rem;
  filter: drop-shadow(0px 0px 5px #d56e19);
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.runDetails {
  height: 100%;
  width: 13rem;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  align-items: center;
}

.runInfo {
  font-size: 1.2rem;
  font-weight: 600;
  margin: 0.3rem;
}

.runButton {
  border: 2px solid  #909090;
  border-radius: 2rem;
  font-size: 1rem;
  padding: 0.7rem;
  background-color: #121212;
  color: #f6f2f2;
  margin-bottom: 0.3rem;
  cursor: pointer;
  font-family:'Ubuntu', sans-serif;
  font-weight: 500;
}

.runButtonClicked {
  border: 2px solid  #909090;
  border-radius: 2rem;
  font-size: 1rem;
  padding: 0.7rem;
  background-color:#ff7300;
  color: #f6f2f2;
  margin-bottom: 0.3rem;
  cursor: pointer;
  font-family:'Ubuntu', sans-serif;
  font-weight: 500;
}


.runButton:hover {
  border: 2px solid orange;
  
  font-size: rem;
}

.divider {
  border-left: 2px solid #000000;
  border-right: 2px solid #000000;
}

.recentRun {
  border-top: 2px solid #000000;
}

.inputContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
}

.info{
  margin-right: 0.5rem;
  font-weight: 600;
}

.info2{
  font-weight: 600;
}

.timeInput {
  width: 3rem;
  margin: 0 0.2rem;
  border: solid #000000;
}

#hours, #minutes, #seconds{
  width: 3rem;
  margin: 0 0.2rem;
  border: solid #000000;
}

.delete {
  border: 2px solid black;
  border-radius: 10px;
  font-size: 1rem;
  margin-top: 0;
  bottom: 0.5rem;
}



@media only screen and (min-width: 750px) and (max-width: 1000px){
  /*
  .formEntry {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
  }*/
  .listRun {
    height: 9rem;
    width: 80%;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  .runButton {
    font-size: 0.8rem;
  }
  
  .runButtonClicked {
    font-size: 0.8rem;
  }
}

@media only screen and (max-width: 750px) {
  h1 {
    font-size: 1.6rem;
  }
  .formRow {
    height: 100%;
    width: 17rem;
    margin: 1rem auto;
  }
