h4{
    margin: 0;
    font-size: 1.3rem;
}

.container {
    height: 100%;
    width: 50%;
    border-radius: 25px ;
    margin: 0 auto 2rem auto;
    border: 5px solid orange;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: column;
    background-image: url('./Runner\ images/OIG (19).jpg');
    background-position: center;
    background-size: cover;
    
}

.paceContainer {
    height: 100%;
    width: 100%;
    border-radius: 25px ;
    margin: 0 auto 2rem auto;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
}

.computeContainer{
    height: 8rem;
    width: 10rem;
    background-color: #f6f2f2;
    
    filter: drop-shadow(0px 0px 5px #d56e19);
    border: 2px solid black;
    border-radius: 25px;
    margin: 0.5rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.paceHeader {
    height: 2rem;
    width: 14rem;
    background-color: #f6f2f2;
    filter: drop-shadow(0px 0px 5px #d56e19);
    border: 2px solid black;
    border-radius: 25px;
    text-align: center;
    padding-top: 0.3rem;
    margin-bottom: 0.5rem;
}

.picture1, .picture2, .picture3 {
    background-position: center;
    background-size: cover;
    height: 8rem;
    width: 10rem;
    border-radius: 10px;
  }
  
.picture1 {
    background-image: url('./Runner\ images/OIG (16).jpg');
  }

.picture2 {
    background-image: url('./Runner\ images/OIG (19).jpg');
  }

.picture3 {
    background-image: url('./Runner\ images/OIG.Up9D5iyFatJp.jpg');
  }

  


@media only screen and (max-width: 700px){
.computeContainer{
    height: 5rem;
    width: 8rem;
    padding: 0 0.5rem;
}

.paceContainer{
    width: 95%;
}

.container {
    width: 95%;
}
}