.welcome {
    font-size: 1.6rem;
    padding: 0;
    text-align: center;
    grid-row: 1 / span 2;
    grid-column: 2 / span 1;
    font-weight: 500;
}

.appInfo {
    font-size: 1.6rem;
    padding: 0;
    text-align: left;
    grid-row: 3 / span 1;
    grid-column: 2 / span 1;
    border: 2px solid orange;
    margin: 0;
    font-weight: 500;
}

.welcomeContainer {
    height: 100%;
    width: 60%;
    border-radius: 25px ;
    margin: 1rem auto 2rem auto;
    border: 5px solid orange;
    display: grid;
    grid-template: repeat(3, 10rem) / repeat(2, 1fr);
    gap: 10px; 
    padding: 1rem;
}
